<template>
  <Web
    :dataset_name="dataset_name"
    :discription="discription"
    :keys="keys"
    :fromInfo="fromInfo"
    :citeInfo="citeInfo"
    :code="code"
    :useInfo="useInfo"
    :datasets_split_info="datasets_split_info"
    :tableData="tableData"
  />
</template>
<script>
import Web from "../components/PathPlaning/DatasetWebV1.vue";
export default {
  name: "PathPlanning",
  components: { Web },
  data() {
    return {
      dataset_name: "避障数据集",
      discription:
        "数据集是专门为了机器人避障训练而创建的。多样化和广泛的训练数据对训练工业场景下的避障能力至关重要。本文的数据集是在仿真环境下收集得到，包含10000条成功路径。这些路径与使用Franka Panda机器人机械臂自上而下的抓取移动物体有关。物体随机放置在XYZ空间中，并需要避开随机生成的障碍物。",
      dataset_viewer: [
        //   { title: "访问量", content: "4", src: require("./view.png") },
        //  { title: "下载量", content: "1", src: require("./down_num.png") }
      ],
      keys: ["避障研究", "机器人", "多样场景"],
      fromInfo: [
        { title: "发布机构", content: "北航ACT实验室" },
        { title: "发布时间", content: "2024/10/22" },
        { title: "更新时间", content: "2024/10/25" },
        {
          title: "下载地址",
          content: "https://download.act.buaa.edu.cn/path_planning/"
        },
        { title: "数据集大小", content: "80MB" },
        { title: "存储格式", content: "hdf5" }
      ],
      citeInfo:
        "@misc{<br> title={机器人避障数据集},<br> url={https://datasets.act.buaa.edu.cn/#/path_planning},<br> author={北航ACT实验室},<br> year={2024}<br>}",
      code:
        'with h5py.File(f"path/to/all_data.hdf5", "w-") as f:<br>   solutions = f["solutions"]',
      useInfo: [
        { title: "实例数量", content: "10000" },
        { title: "相关任务", content: "路径规划" },
        { title: "特征", content: "7" }
      ],
      datasets_split_info: [
        {
          title: "完整数据集",
          content: "200个包含50条路径的文件",
          src: "https://download.act.buaa.edu.cn/path_planning/all_data.rar"
        },
        {
          title: "训练集",
          content: "180个包含45条路径的文件",
          src: "https://download.act.buaa.edu.cn/path_planning/train_data.hdf5"
        },
        {
          title: "测试集",
          content: "180个包含5条路径的文件，以及20个包含50条路径的文件",
          src: "https://download.act.buaa.edu.cn/path_planning/test_data.hdf5"
        }
      ],
      tableData: [
        { key: "m_length", dim: "(1)", value: "不定长关节序列的实际长度" },
        {
          key: "solutions",
          dim: "(300,7)",
          value:
            "关节序列信息。第一维度是序列长度，第二维度是机器人关节数量。仅m_length之前的序列具备意义，m_length之后的序列以0填充"
        },
        {
          key: "sphere_centers",
          dim: "(2,3)",
          value: "两个球体障碍物的中心位置(x,y,z)"
        },
        {
          key: "sphere_radiis",
          dim: "(2)",
          value:
            "两个球体障碍物的半径,取值范围为(0.1,0.2)。让对应的值为0时意味着不存在该障碍物，为默认填充"
        },
        {
          key: "cube_centers",
          dim: "(2,3)",
          value: "两个正方体障碍物的中心位置(x,y,z)"
        },
        {
          key: "cube_sizes",
          dim: "(2)",
          value:
            "两个球体障碍物的半径,取值范围为(0.1,0.2)。让对应的值为0时意味着不存在该障碍物，为默认填充"
        }
      ],
      data_colums: [
        { title: "m_length", content: "一个[1]的数字，存储了路径长度" },
        {
          title: "solutions",
          content:
            "一个[300,7]的数组，存储了机器人关节序列，第一维度是序列长度，第二维度是机器人关节数量。仅m_length之前的序列具备意义，m_length之后的序列以0填充"
        },
        {
          title: "sphere_centers",
          content: "一个[2,3]的数组，存储了两个球形障碍物的中心位置"
        },
        {
          title: "sphere_radiis",
          content:
            "一个[2]的数组，存储了两个球形障碍物的半径。让对应的值为0时意味着不存在该障碍物，为默认填充"
        },
        {
          title: "cube_centers",
          content: "一个[2,3]的数组，存储了两个正方形障碍物的中心位置"
        },
        {
          title: "cube_sizes",
          content:
            "一个[2]的数组，存储了两个正方形障碍物的半径。让对应的值为0时意味着不存在该障碍物，为默认填充"
        }
      ]
    };
  }
};
</script>
<style></style>



